function isMobileSafari() {    // 判断是否为移动端的safari
  let hasSafari = /Safari/.test(navigator.userAgent),
      noChrome = !/Chrome/.test(navigator.userAgent),
      noQQ = !/MQQBrowser/.test(navigator.userAgent),
      noFireFox = !/FxiOS/.test(navigator.userAgent),
      noCriOS = !/CriOS/.test(navigator.userAgent) // chrome专属
  return hasSafari && noChrome && noQQ && noFireFox && noCriOS
}

function getIOSVersion() {
    let str= navigator.userAgent.toLowerCase()
    let ver=str.match(/cpu iphone os (.*?) like mac os/)
    let version = ver ? ver[1].replace(/_/g,".") : null
    return parseFloat(version)
}
function isQQBrowser() {
    return /MQQBrowser/.test(navigator.userAgent)
}

function sleep(interval) {
  return new Promise(resolve => {
    setTimeout(resolve, interval);
  })
}


export async function _myapp(param) {
  let ua = navigator.userAgent
  let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1,
    isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
    if(isWeiXins()) {
      alert("DEMO，请在移动端的浏览器查看！")
      return
    } else {
      if(isiOS) {
        if(isMobileSafari) { //手机safari
          location.href='itms-apps://itunes.apple.com/app/id544673497'
        } else {
          location.href = `mcloud://outLinks?outlink=${param}`
          await sleep(1000) // 休眠2.5s
          setTimeout(() => {
            if(!(document.hidden || document.webkitHidden)) {
              // 唤起失败
              location.href='itms-apps://itunes.apple.com/app/id544673497'
            }
          },1000)
        }
      } else if(isAndroid) {
        location.href = `hecaiyun://launch//outlinks?outlink=${param}`
        await sleep(2500) // 休眠2.5s
        if(!(document.hidden || document.webkitHidden)) {
          // 唤起失败
          location.href='http://caiyun.feixin.10086.cn:7070/portal/client_new.jsp?v=mCloud_800'
        }
      }
    }
}
export function isWeiXins(){
  var ua = window.navigator.userAgent.toLowerCase();
  if(ua.match(/MicroMessenger/i) == 'micromessenger'){
    return true;
  }else{
    return false;
  }
}



